import React, { Component } from 'react'
import UserPreview from '../components/UserPreview'
import fetchUtil from 'utils/fetchUtil'
import { getDomain } from 'utils/domain'

export const Promised = (Wrapped) => class extends Component {
  state = {
    preview: {
      elements: [],
      layouts: []
    }
  }

  async componentDidMount () {
    try {
      const apiUrl = getDomain('/app/v1/bo/v1/web/merchant/page/' + this.props.params.pageID)
      const data = await fetchUtil.getJSON(apiUrl)

      if (data.viewDataDraft) {
        if (typeof data.viewDataDraft === 'string') {
          data.viewDataDraft = JSON.parse(data.viewDataDraft)
        }
        this.setState({ preview: data.viewDataDraft })
      }
    } catch (e) {
      console.log('---UserView data error')
      console.log(e)
      // this.setState({ preview: UserViewData })
    }
  }

  render () {
    return <Wrapped {...this.state.preview} />
  }
}

export default Promised(UserPreview)



